@import "../variables";
@import "./reset.scss";
@import "./normalaize.scss";
@import "./box.scss";

.container {
  display: grid;
}

@media (min-width: 600px) {
  .container {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (min-width: 900px) {
  .container {
    grid-template-columns: repeat(3, minmax(300px, 1fr));
  }
}
